import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import runtimeEnv from "@mars/heroku-js-runtime-env";
const env = runtimeEnv();
console.log(env.REACT_APP_URL);
console.log(window.location.host);
export default class Home extends Component {
  state = {
    redirect: false,
    to: "",
  };
  setRedirect = (url) => {
    this.setState({
      redirect: true,
      to: url,
    });
  };
  renderRedirect = () => {
    if (this.state.redirect) {
      return <Redirect to={this.state.to} />;
    }
  };
  componentDidMount() {
    const favicon = document.getElementById("FavIcon");
    if (
      env.REACT_APP_FAVICONURL !== undefined ||
      env.REACT_APP_FAVICONURL !== ""
    )
      favicon.href = `${env.REACT_APP_FAVICONURL}`;
    const title = document.getElementById("AppTitle");
    if (env.REACT_APP_TITLE !== undefined || env.REACT_APP_TITLE !== "")
      title.innerText = `${env.REACT_APP_TITLE}`;
    else {
      title.innerText = "Media Login Panel";
    }
  }
  render() {
    return (
      <div className="login_section" style={{ height: 350 }}>
        <p
          style={{ color: "#f27c89", fontWeight: 600, fontSize: 24 }}
          className="text-center mb-5"
        >
          Please Select A Login Option
        </p>
        {this.renderRedirect()}
        <div className="row btns_row">
          {env.REACT_APP_INSTAGRAM === "true" ? (
            <div className="col-6 col-md-6">
              <button
                type="button"
                className="btn login_btn choose_btns"
                onClick={() => this.setRedirect("/Instagram")}
              >
                Instagram
              </button>
            </div>
          ) : (
            ""
          )}
          {env.REACT_APP_LINKEDIN === "true" ? (
            <div className="col-6 col-md-6">
              <button
                type="button"
                className="btn login_btn choose_btns float_right"
                onClick={() => this.setRedirect("/Linkedin")}
              >
                Linkedin
              </button>
            </div>
          ) : (
            ""
          )}
        </div>
        <div className="row btns_row middle_btn_row">
          {env.REACT_APP_FACEBOOK === "true" ? (
            <div className="col-6 col-md-6 p-0">
              <button
                type="button"
                className="btn login_btn choose_btns pink_clr"
                onClick={() => this.setRedirect("/Facebook")}
              >
                Facebook
              </button>
            </div>
          ) : (
            ""
          )}
          {env.REACT_APP_TWITTER === "true" ? (
            <div className="col-6 col-md-6 p-0">
              <button
                type="button"
                className="btn login_btn choose_btns pink_clr float_right"
                onClick={() => this.setRedirect("/Twitter")}
              >
                Twitter
              </button>
            </div>
          ) : (
            ""
          )}
        </div>
        <div className="row btns_row">
          {env.REACT_APP_PINTEREST === "true" ? (
            <div className="col-12 col-md-12">
              <button
                type="button"
                className="btn login_btn choose_btns"
                onClick={() => this.setRedirect("/Pinterest")}
              >
                Pinterest
              </button>
            </div>
          ) : (
            ""
          )}
        </div>
      </div>
    );
  }
}
